.list_link {
  display: flex;
  gap: 16px;
}

.details_link {
  display: block;
  color: var(--header-white-color);
  padding: 8px 16px;
  border-radius: 10px;
  border: 1px solid var(--header-white-color);
}

@media (min-width: 325px) and (max-width: 768px) {
  .list_link {
    justify-content: center;
  }
}
